export const categories: ICategory[] = [
  {
    label: 'All',
    id: 'all'
  },
  {
    label: 'Hotel',
    id: 'hotel'
  },
  {
    label: 'Interior',
    id: 'interior'
  },
  {
    label: 'Food',
    id: 'food'
  },
  {
    label: 'Video',
    id: 'video'
  },
  {
    label: 'Others',
    id: 'others'
  }
];

export const isExistId = (_id: string): boolean => {
  const category: ICategory | undefined = categories.find(
    ({ id }): boolean => id === _id
  );
  return !!category?.id;
};

export const idToLabel = (_id: string): string => {
  const category: ICategory | undefined = categories.find(
    ({ id }): boolean => id === _id
  );

  if (category?.label) {
    return category?.label;
  } else {
    // 該当するカテゴリーがない場合は all として返す
    return 'All';
  }
};

export const labelToId = (_label: string): string | undefined => {
  const category: ICategory | undefined = categories.find(
    ({ label }: ICategory): boolean => label === _label
  );
  return category?.id;
};
