import dataFile from '../../data.json';

const $ = (data: any) => {
  const { name } = data;

  const [, _link] = name.match(/^portfolio_works_(\d{8}_.+)$/);
  const link: string = _link.replace(/_/g, '-');

  return {
    ...data,
    link
  };
};

export const works = dataFile.map((data: any) => $(data));
